import React, { useState, useEffect, useCallback } from 'react';
import LocalStorageService from '../services/LocalStorageService';
import WishlistCard from '../components/WishlistCard';
import MovieModal from '../components/MovieModal';
import '../styles/Wishlist.css';

const Wishlist = () => {
  const [wishlist, setWishlist] = useState([]);
  const [selectedMovie, setSelectedMovie] = useState(null); // ÏÑ†ÌÉùÎêú ÏòÅÌôî
  const [isModalOpen, setIsModalOpen] = useState(false); // Î™®Îã¨ ÏÉÅÌÉú
  const [page, setPage] = useState(1); // ÌéòÏù¥ÏßÄ ÏÉÅÌÉú Ï∂îÍ∞Ä
  const [loading, setLoading] = useState(false); // Î°úÎî© ÏÉÅÌÉú Ï∂îÍ∞Ä

  // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú Ï∞úÌïú ÏòÅÌôî Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞
  const loadWishlist = useCallback(() => {
    setLoading(true);
    const storedWishlist = LocalStorageService.get('wishlist') || [];
    setWishlist(storedWishlist.slice((page - 1) * 10, page * 10)); // prev Ï†úÍ±∞
    setLoading(false);
  }, [page]);

  useEffect(() => {
    loadWishlist();
  }, [loadWishlist]);

  // Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
  const handleScroll = () => {
    if (window.innerHeight + document.documentElement.scrollTop !== document.documentElement.offsetHeight || loading) return;
    setPage((prev) => prev + 1); // Îã§Ïùå ÌéòÏù¥ÏßÄ Î°úÎìú
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [handleScroll]);

  // Ï∞úÌïú ÏòÅÌôî Î™©Î°ùÏóêÏÑú ÏÇ≠Ï†ú
  const handleRemoveFromWishlist = (movie) => {
    const storedWishlist = LocalStorageService.get('wishlist') || [];
    const updatedWishlist = storedWishlist.filter((item) => item.id !== movie.id);
    LocalStorageService.set('wishlist', updatedWishlist);
    setWishlist(updatedWishlist.slice(0, page * 10)); // ÌòÑÏû¨ ÌéòÏù¥ÏßÄÍπåÏßÄÎßå ÌëúÏãú
  };

  // ÏòÅÌôî ÌÅ¥Î¶≠ Ïãú Î™®Îã¨ Ïó¥Í∏∞
  const handleCardClick = (movie) => {
    setSelectedMovie(movie);
    setIsModalOpen(true);
  };

  // Î™®Îã¨ Îã´Í∏∞ (ÌéòÏù¥ÏßÄ Î¶¨Î°úÎìú)
  const handleCloseModal = () => {
    setSelectedMovie(null);
    setIsModalOpen(false);

    // Ï∞ú Î™©Î°ù Îã§Ïãú Î°úÎìú (Î¶¨Î°úÎî© ÎåÄÏã† ÏóÖÎç∞Ïù¥Ìä∏ Î∞©Ïãù ÏÇ¨Ïö©)
    setWishlist(LocalStorageService.get('wishlist') || []);
  };

  return (
    <div className="wishlist">
      <h1>‚úÖ Ï∞ú Î™©Î°ù</h1>
      {wishlist.length === 0 ? (
        <p>Ï∞úÌïú ÏòÅÌôîÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
      ) : (
        <div className="movie-grid">
          {wishlist.map((movie) => (
            <WishlistCard
              key={movie.id}
              movie={movie}
              onRemoveFromWishlist={handleRemoveFromWishlist}
              onCardClick={() => handleCardClick(movie)} // Ïπ¥Îìú ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Ï†ÑÎã¨
            />
          ))}
        </div>
      )}
      {loading && <p>Î°úÎî© Ï§ë...</p>} {/* Î°úÎî© Ï§ë Î©îÏãúÏßÄ Ï∂îÍ∞Ä */}

      {/* MovieModal Î†åÎçîÎßÅ */}
      {isModalOpen && selectedMovie && (
        <MovieModal
          movie={selectedMovie}
          onClose={handleCloseModal} // Î™®Îã¨ Îã´Ìûò Ïãú Ìò∏Ï∂ú
        />
      )}
    </div>
  );
};

export default Wishlist;
