import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import LocalStorageService from '../services/LocalStorageService';
import '../styles/MovieModal.css';

const MovieModal = ({ movie, onClose }) => {
  const navigate = useNavigate();
  const { id, title, overview, release_date, vote_average, poster_path } = movie;
  const [isWishlisted, setIsWishlisted] = useState(false);
  const [detailedInfo, setDetailedInfo] = useState(null);
  const [error, setError] = useState(null);

  // API ÌÇ§ Í∞ÄÏ†∏Ïò§Í∏∞
  const getApiKey = () => {
    const user = JSON.parse(localStorage.getItem('loggedInUser'));
    return user?.apiKey;
  };

  // ÏòÅÌôî ÏÑ∏Î∂Ä Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
  useEffect(() => {
    const fetchMovieDetails = async () => {
      const apiKey = getApiKey();
      
      if (!apiKey) {
        setError('API ÌÇ§Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.');
        return;
      }

      try {
        const response = await axios.get(
          `https://api.themoviedb.org/3/movie/${id}`,
          {
            params: {
              api_key: apiKey,
              language: 'ko-KR'
            }
          }
        );
        setDetailedInfo(response.data);
      } catch (error) {
        console.error('Error fetching movie details:', error);
        setError('ÏòÅÌôî Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      }
    };

    fetchMovieDetails();
  }, [id]);

  // Ï¥àÍ∏∞ Ï∞ú ÏÉÅÌÉú ÌôïÏù∏
  useEffect(() => {
    const wishlist = LocalStorageService.get('wishlist') || [];
    setIsWishlisted(wishlist.some((item) => item.id === id));
  }, [id]);

  // Ï∞ú ÏÉÅÌÉú ÌÜ†Í∏Ä
  const handleWishlist = () => {
    const wishlist = LocalStorageService.get('wishlist') || [];

    if (isWishlisted) {
      // Ï∞ú Ìï¥Ï†ú
      const updatedWishlist = wishlist.filter((item) => item.id !== id);
      LocalStorageService.set('wishlist', updatedWishlist);
      alert(`${title}ÏùÑ(Î•º) Ï∞ú Î™©Î°ùÏóêÏÑú Ï†úÍ±∞ÌñàÏäµÎãàÎã§!`);
    } else {
      // Ï∞ú Ï∂îÍ∞Ä
      const newMovieData = {
        id,
        title,
        overview,
        release_date,
        vote_average,
        poster_path,
      };
      wishlist.push(newMovieData);
      LocalStorageService.set('wishlist', wishlist);
      alert(`${title}ÏùÑ(Î•º) Ï∞ú Î™©Î°ùÏóê Ï∂îÍ∞ÄÌñàÏäµÎãàÎã§!`);
    }

    setIsWishlisted(!isWishlisted);
  };

  if (error) {
    return (
      <div className="modal-overlay" onClick={onClose}>
        <div className="modal-content">
          <button className="close-button" onClick={onClose}>√ó</button>
          <p className="error-message">{error}</p>
        </div>
      </div>
    );
  }

  if (!detailedInfo) {
    return (
      <div className="modal-overlay" onClick={onClose}>
        <div className="modal-content">
          <button className="close-button" onClick={onClose}>√ó</button>
          <p className="loading-message">Î°úÎî©Ï§ë...</p>
        </div>
      </div>
    );
  }

  const {
    original_title,
    runtime,
    genres,
    vote_count,
    original_language,
    production_companies,
    budget,
    revenue,
  } = detailedInfo;

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <button className="close-button" onClick={onClose}>
          √ó
        </button>
        <div className="modal-header">
          <img
            src={`https://image.tmdb.org/t/p/w500/${poster_path}`}
            alt={title}
            className="modal-poster"
          />
          <div className="modal-info">
            <h2>{title}</h2>
            <p>
              <strong>ÏõêÏ†úÎ™©:</strong> {original_title}
            </p>
            <p>
              <strong>Í∞úÎ¥âÏùº:</strong> {release_date}
            </p>
            <p>
              <strong>ÌèâÏ†ê:</strong> {vote_average} ({vote_count}Î™Ö Ìà¨Ìëú)
            </p>
            <p>
              <strong>Ïû•Î•¥:</strong> {genres.map((genre) => genre.name).join(', ')}
            </p>
            <p>
              <strong>Îü¨Îãù ÌÉÄÏûÑ:</strong> {runtime}Î∂Ñ
            </p>
            <p>
              <strong>Ïñ∏Ïñ¥:</strong> {original_language.toUpperCase()}
            </p>
            <p>
              <strong>Ï†úÏûëÏÇ¨:</strong>{' '}
              {production_companies.length
                ? production_companies.map((company) => company.name).join(', ')
                : 'Ï†ïÎ≥¥ ÏóÜÏùå'}
            </p>
            <p>
              <strong>ÏòàÏÇ∞:</strong> ${budget.toLocaleString()}
            </p>
            <p>
              <strong>ÏàòÏùµ:</strong> ${revenue.toLocaleString()}
            </p>
            <p>
              <strong>Ï§ÑÍ±∞Î¶¨:</strong> {overview || 'Ï§ÑÍ±∞Î¶¨Í∞Ä ÏóÜÏäµÎãàÎã§.'}
            </p>
            <button
              className={`wishlist-button ${isWishlisted ? 'active' : ''}`}
              onClick={handleWishlist}
            >
              {isWishlisted ? 'Ï∞ú Ìï¥Ï†ú' : 'Ï∞úÌïòÍ∏∞'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MovieModal;
