import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import TMDbAPI from '../services/URL.ts';
import MovieSlider from '../components/MovieSlider';
import MovieModal from '../components/MovieModal';
import '../styles/Home.css';

const Home = () => {
  const navigate = useNavigate();
  const [bannerMovie, setBannerMovie] = useState(null); // Î©îÏù∏ Î∞∞ÎÑà ÏòÅÌôî
  const [popularMovies, setPopularMovies] = useState([]); // Ïù∏Í∏∞ ÏòÅÌôî
  const [genreMovies, setGenreMovies] = useState([]); // Î°úÎìúÎêú Ïû•Î•¥Î≥Ñ ÏòÅÌôî
  const [loadedGenres, setLoadedGenres] = useState([]); // Ïù¥ÎØ∏ Î°úÎìúÎêú Ïû•Î•¥
  const [genres, setGenres] = useState([]); // Ï†ÑÏ≤¥ Ïû•Î•¥ Î™©Î°ù
  const [wishlist, setWishlist] = useState([]); // Ï∞ú Î™©Î°ù
  const [selectedMovie, setSelectedMovie] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [apiKey, setApiKey] = useState(() => {
    const loggedInUser = JSON.parse(localStorage.getItem('loggedInUser'));
    return loggedInUser?.apiKey || null;
  });

  // Ï∞ú Î™©Î°ù Î°úÎìú Ìï®Ïàò
  const loadWishlist = () => {
    const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    setWishlist(storedWishlist);
  };

  useEffect(() => {
    loadWishlist(); // Ï¥àÍ∏∞ Î°úÎìú Ïãú Ï∞ú Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
  }, []);

  useEffect(() => {
    if (apiKey) {
      const fetchInitialData = async () => {
        try {
          const popularData = await TMDbAPI.getPopularMovies(apiKey);
          setPopularMovies(popularData.results.slice(0, 10));
          setBannerMovie(
            popularData.results[Math.floor(Math.random() * popularData.results.length)]
          );

          const genreData = await TMDbAPI.getGenres(apiKey);
          setGenres(genreData.genres);
          loadNextGenres(genreData.genres.slice(0, 5));
        } catch (error) {
          console.error('Error fetching initial data:', error);
        }
      };

      fetchInitialData();
    }
  }, [apiKey]);

  const loadNextGenres = async (nextGenres) => {
    const newGenreMovies = [];
    for (const genre of nextGenres) {
      try {
        const movies = await TMDbAPI.getMoviesByGenre(apiKey, genre.id);
        newGenreMovies.push({ genreName: genre.name, movies: movies.results.slice(0, 10) });
      } catch (error) {
        console.error(`Error loading movies for genre: ${genre.name}`, error);
      }
    }
    setGenreMovies((prev) => [...prev, ...newGenreMovies]);
    setLoadedGenres((prev) => [...prev, ...nextGenres]);
  };

  const handleMovieClick = (movie) => {
    setSelectedMovie(movie);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setSelectedMovie(null);
    setIsModalOpen(false);
    loadWishlist(); // Î™®Îã¨ Îã´Ìûê Îïå Ï∞ú Î™©Î°ù Îã§Ïãú Î°úÎìú
  };

  return (
    <div className="home">
      {bannerMovie && (
        <div
          className="banner"
          style={{
            backgroundImage: `url(https://image.tmdb.org/t/p/original/${bannerMovie.backdrop_path})`,
          }}
        >
          <div className="banner-overlay">
            <div className="banner-content">
              <h1>{bannerMovie.title}</h1>
              <p>{bannerMovie.overview}</p>
              <div className="banner-buttons">
                <button className="play-button" onClick={() => alert('Ïû¨ÏÉù Í∏∞Îä•ÏùÄ ÏïÑÏßÅ Íµ¨ÌòÑÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.')}>
                  Ïû¨ÏÉù
                </button>
                <button
                  className="details-button"
                  onClick={() => handleMovieClick(bannerMovie)}
                >
                  ÏÉÅÏÑ∏Ï†ïÎ≥¥
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
      <MovieSlider title="ÏßÄÍ∏à Îú®Îäî ÏΩòÌÖêÏ∏†" movies={popularMovies} onMovieClick={handleMovieClick} wishlist={wishlist} />
      {genreMovies.map(({ genreName, movies }) => (
        <MovieSlider
          key={genreName}
          title={genreName}
          movies={movies}
          onMovieClick={handleMovieClick}
          wishlist={wishlist}
        />
      ))}
      {isModalOpen && selectedMovie && (
        <MovieModal movie={selectedMovie} onClose={handleCloseModal} />
      )}
    </div>
  );
};

export default Home;
