import React, { useEffect, useState } from 'react';
import '../styles/MovieCard.css';

const MovieCard = ({ movie, onClick }) => {
  const { poster_path, title, id } = movie;
  const [isWishlisted, setIsWishlisted] = useState(false);

  // Ï∞ú Î™©Î°ù Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú Î∂àÎü¨Ïò§Í∏∞
  useEffect(() => {
    const wishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    const isInWishlist = wishlist.some((item) => item.id === id);
    setIsWishlisted(isInWishlist);
  }, [id]);

  // Ï∞úÌïòÍ∏∞ Î≤ÑÌäº ÌÅ¥Î¶≠ Ï≤òÎ¶¨
  const handleWishlistClick = (e) => {
    e.stopPropagation(); // Î∂ÄÎ™® ÏöîÏÜåÎ°úÏùò ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Ï†ÑÌåå Î∞©ÏßÄ
    
    let wishlist = JSON.parse(localStorage.getItem('wishlist')) || [];

    if (isWishlisted) {
      // Ïù¥ÎØ∏ Ï∞ú Î™©Î°ùÏóê ÏûàÎäî Í≤ΩÏö∞ -> Ï†úÍ±∞
      wishlist = wishlist.filter((item) => item.id !== id);
      setIsWishlisted(false);
    } else {
      // Ï∞ú Î™©Î°ùÏóê ÏóÜÎäî Í≤ΩÏö∞ -> Ï∂îÍ∞Ä
      wishlist.push(movie);
      setIsWishlisted(true);
    }

    // Ï∞ú Î™©Î°ù Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÏóÖÎç∞Ïù¥Ìä∏
    localStorage.setItem('wishlist', JSON.stringify(wishlist));
  };

  return (
    <div className="movie-card">
      <img
        src={`https://image.tmdb.org/t/p/w500/${poster_path}`}
        alt={title || "Movie Poster"}
        className={`movie-poster ${isWishlisted ? 'wishlisted' : ''}`}
        onClick={onClick}
      />
      <div className="movie-details">
        <button
          className={`wishlist-btn ${isWishlisted ? 'disabled' : ''}`}
          onClick={handleWishlistClick}
        >
          {isWishlisted ? '‚úÖ Ï∞ú ÏôÑÎ£å' : 'Ï∞úÌïòÍ∏∞'}
        </button>
      </div>
    </div>
  );
};

export default MovieCard;
