import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Toast from '../components/Toast';
import ReactMarkdown from 'react-markdown';
import '../styles/SignIn.css';
import termsText from '../terms/terms.md';
import '../styles/TermsModal.css';

const TMDB_API_KEY = "0dddad40b95585e608004ae19df81bd2";
const KAKAO_KEY = "91d4fd298713928628e55b677c118bd3";

const SignIn = () => {
  const [isSignUp, setIsSignUp] = useState(false);
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    apiKey: '',
    username: '',
    rememberMe: false,
    agreeTerms: false,
  });
  const [errors, setErrors] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    apiKey: '',
    username: '',
    agreeTerms: ''
  });
  const [toast, setToast] = useState({ message: '', type: '' });
  const [showTermsModal, setShowTermsModal] = useState(false);
  const [isClosing, setIsClosing] = useState(false); // Î™®Îã¨ Îã´Ìûò Ïï†ÎãàÎ©îÏù¥ÏÖò ÏÉÅÌÉú
  const [termsContent, setTermsContent] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    // Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Îêú ÏÇ¨Ïö©Ïûê Ï≤¥ÌÅ¨
    const loggedInUser = JSON.parse(localStorage.getItem('loggedInUser'));
    if (loggedInUser) {
      navigate('/');
      return;
    }

    // ÏûêÎèô Î°úÍ∑∏Ïù∏ Ï≤¥ÌÅ¨
    const rememberedUser = JSON.parse(localStorage.getItem('rememberMe'));
    if (rememberedUser) {
      const users = JSON.parse(localStorage.getItem('users')) || [];
      const user = users.find((u) => u.email === rememberedUser.email);
      
      if (user) {
        // 30Ïùº Ïù¥ÎÇ¥Ïù∏ Í≤ΩÔøΩÔøΩÏóêÎßå ÏûêÎèô Î°úÍ∑∏Ïù∏
        const thirtyDays = 30 * 24 * 60 * 60 * 1000; // 30ÏùºÏùÑ Î∞ÄÎ¶¨Ï¥àÎ°ú Î≥ÄÌôò
        if (new Date().getTime() - rememberedUser.timestamp < thirtyDays) {
          localStorage.setItem('loggedInUser', JSON.stringify(user));
          navigate('/');
        } else {
          // 30ÏùºÏù¥ ÏßÄÎÇú Í≤ΩÏö∞ rememberMe Îç∞Ïù¥ÌÑ∞ ÏÇ≠Ï†ú
          localStorage.removeItem('rememberMe');
        }
      }
    }
  }, [navigate]);

  useEffect(() => {
    // Ïπ¥Ïπ¥Ïò§ SDK Ï¥àÍ∏∞Ìôî
    if (window.Kakao) {
      if (!window.Kakao.isInitialized()) {
        window.Kakao.init(KAKAO_KEY);
      }
    }
  }, []);

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const validateForm = () => {
    const { email, password, confirmPassword, agreeTerms, apiKey } = formData;
    let newErrors = {};

    if (!validateEmail(email)) {
      newErrors.email = 'Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ïù¥Î©îÏùº ÌòïÏãùÏûÖÎãàÎã§.';
    }
    if (isSignUp && password !== confirmPassword) {
      newErrors.confirmPassword = 'ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.';
    }
    if (isSignUp && !agreeTerms) {
      newErrors.agreeTerms = 'ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï¥Ïïº Ìï©ÎãàÎã§.';
    }
    if (isSignUp && !apiKey) {
      newErrors.apiKey = 'API ÌÇ§Î•º ÏûÖÎ†•Ìï¥Ïïº Ìï©ÎãàÎã§.';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value,
    });
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      if (isSignUp) {
        handleSignUp();
      } else {
        handleSignIn();
      }
    }
  };

  const handleSignUp = () => {
    setErrors({});
    const { email, password, confirmPassword, apiKey, agreeTerms, username } = formData;
    const errors = {};
  
    if (!validateEmail(email)) errors.email = 'Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ïù¥Î©îÏùº ÌòïÏãùÏûÖÎãàÎã§.';
    if (password !== confirmPassword) errors.confirmPassword = 'ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.';
    if (!agreeTerms) errors.agreeTerms = 'ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï¥Ïïº Ìï©ÎãàÎã§.';
    if (!apiKey) errors.apiKey = 'API ÌÇ§Î•º ÏûÖÎ†•Ìï¥Ïïº Ìï©ÎãàÎã§.';
    if (!username) errors.username = 'ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ïïº Ìï©ÎãàÎã§.';
  
    if (Object.keys(errors).length > 0) {
      setErrors(errors);
      return;
    }
  
    const users = JSON.parse(localStorage.getItem('users')) || [];
    if (users.some(user => user.email === email)) {
      setErrors({ email: 'Ïù¥ÎØ∏ Îì±Î°ùÎêú Ïù¥Î©îÏùºÏûÖÎãàÎã§.' });
      return;
    }
  
    // ÏÉà ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Ïóê wishlist Ï∂îÍ∞Ä
    const newUser = {
      email,
      password,
      apiKey,
      username,
      wishlist: []
    };
  
    users.push(newUser);
    localStorage.setItem('users', JSON.stringify(users));
    setToast({ message: 'ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µ!', type: 'success' });
    setTimeout(() => {
      setIsSignUp(false); // Î°úÍ∑∏Ïù∏ ÌèºÏúºÎ°ú Ï†ÑÌôò
    }, 2000);
  };
  const handleSignIn = () => {
    setErrors({});
    const { email, password, rememberMe } = formData;
  
    if (!validateEmail(email)) {
      setErrors({ email: 'Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ïù¥Î©îÏùº ÌòïÏãùÏûÖÔøΩÔøΩÎã§.' });
      return;
    }
  
    const users = JSON.parse(localStorage.getItem('users')) || [];
    const user = users.find((u) => u.email === email && u.password === password);
  
    if (!user) {
      setErrors({ password: 'Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.' });
      return;
    }
  
    // Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ Ï†ÄÏû• Ïãú ÌïÑÏöîÌïú Î™®Îì† Ï†ïÎ≥¥Î•º Ìè¨Ìï®
    const loginData = {
      email: user.email,
      username: user.username,
      apiKey: user.apiKey,
      wishlist: user.wishlist || []
    };
  
    console.log('Î°úÍ∑∏Ïù∏ Îç∞Ïù¥ÌÑ∞:', loginData); // ÎîîÎ≤ÑÍπÖÏö©
    localStorage.setItem('loggedInUser', JSON.stringify(loginData));
  
    if (rememberMe) {
      localStorage.setItem('rememberMe', JSON.stringify({
        email: user.email,
        timestamp: new Date().getTime()
      }));
    } else {
      localStorage.removeItem('rememberMe');
    }
  
    setToast({ message: 'Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ! Î©îÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.', type: 'success' });
    setTimeout(() => {
      navigate('/');
    }, 2000);
  };
  const handleCardSwitch = () => {
    setIsSignUp((prev) => !prev);
    setErrors({}); // ÏóêÎü¨ Ï¥àÍ∏∞Ìôî
  };
  useEffect(() => {
    setErrors({});
  }, [isSignUp]);

  useEffect(() => {
    validateForm();
  }, [formData]);

  useEffect(() => {
    // Load terms and conditions from a separate file
    fetch(termsText)
      .then((response) => response.text())
      .then((text) => setTermsContent(text))
      .catch((error) => console.error('Ïù¥Ïö©ÏïΩÍ¥Ä Î°úÎî© Ïò§Î•ò:', error));
  }, []);
  
  const handleCloseModal = () => {
    setIsClosing(true);
    setTimeout(() => {
      setShowTermsModal(false);
      setIsClosing(false);
    }, 500); // Ïï†ÎãàÎ©îÏù¥ÏÖò ÏãúÍ∞Ñ (0.5Ï¥à) ÌõÑÏóê Î™®Îã¨ÏùÑ Ïà®ÍπÄ
  };

  const handleKakaoLogin = () => {
    if (!window.Kakao) {
      setToast({ message: 'Ïπ¥Ïπ¥Ïò§ SDK Î°úÎìú Ïã§Ìå®', type: 'error' });
      return;
    }

    window.Kakao.Auth.login({
      success: (authObj) => {
        window.Kakao.API.request({
          url: '/v2/user/me',
          success: (response) => {
            const kakaoUserData = {
              id: response.id,
              nickname: response.properties?.nickname,
              profileImage: response.properties?.profile_image,
              apiKey: TMDB_API_KEY
            };
            
            localStorage.setItem('kakaoUser', JSON.stringify(kakaoUserData));
            setToast({ message: 'Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ!', type: 'success' });
            
            setTimeout(() => {
              navigate('/');
            }, 2000);
          },
          fail: (error) => {
            console.error('Ïπ¥Ïπ¥Ïò§ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÏöîÏ≤≠ Ïã§Ìå®:', error);
            setToast({ message: 'Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ Ïã§Ìå®', type: 'error' });
          }
        });
      },
      fail: (error) => {
        console.error('Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ Ïã§Ìå®:', error);
        setToast({ message: 'Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ Ïã§Ìå®', type: 'error' });
      }
    });
  };

  return (
    <div className="signin-container">
      <div className="card-wrapper">
        <div className={`card-container ${isSignUp ? 'sign-up' : 'sign-in'}`}>
          {/* Î°úÍ∑∏Ïù∏ Ïπ¥Îìú */}
          <div className="card front">
            <h2>ÌôòÏòÅÌï©ÎãàÎã§!</h2>
            <br />
            <p>Î≥∏ ÏÑúÎπÑÏä§Îäî Ìé∏Î¶¨ÌïòÍ≥† Ìö®Ïú®Ï†ÅÏù∏ Ïù¥Ïö©ÏùÑ ÏúÑÌï¥</p>
            <p>Í∞úÏù∏ TMDB APIÎ°ú ÏòÅÌôî Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨ÏòµÎãàÎã§.</p>
            <br />
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              placeholder="Ïù¥Î©îÏùº"
            />
            {errors.email && <div className="error">{errors.email}</div>}
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏"
            />
            {errors.password && <div className="error">{errors.password}</div>}
            <label>
              <input
                type="checkbox"
                name="rememberMe"
                checked={formData.rememberMe}
                onChange={handleChange}
              />
              <span>Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú Ïú†ÏßÄ</span>
            </label>
            <button onClick={handleSignIn}>Î°úÍ∑∏Ïù∏</button>
            <button 
              onClick={handleKakaoLogin}
              className="kakao-login-btn"
            >
              Ïπ¥Ïπ¥Ïò§Î°ú ÏãúÏûëÌïòÍ∏∞
            </button>
            <br />
            <span onClick={handleCardSwitch}>Í≥ÑÏ†ïÏù¥ ÏóÜÏúºÏã†Í∞ÄÏöî? ÌöåÏõêÍ∞ÄÏûÖ</span>
          </div>

          {/* ÌöåÏõêÍ∞ÄÏûÖ Ïπ¥Îìú */}
          <div className="card back">
            <h2>Í≥ÑÏ†ï ÏÉùÏÑ±</h2>
            <input
              type="text"
              name="username"
              value={formData.username}
              onChange={handleChange}
              placeholder="ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ"
            />
            {errors.username && <div className="error">{errors.username}</div>}
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              placeholder="Ïù¥Î©îÏùº"
            />
            {errors.email && <div className="error">{errors.email}</div>}
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏"
            />
            <input
              type="password"
              name="confirmPassword"
              value={formData.confirmPassword}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏"
            />
            <input
              type="text"
              name="apiKey"
              value={formData.apiKey}
              onChange={handleChange}
              placeholder="TMDB API ÌÇ§"
            />
            <label>
              <input
                type="checkbox"
                name="agreeTerms"
                checked={formData.agreeTerms}
                onChange={handleChange}
              />
              <span>ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï©ÎãàÎã§</span>
            </label>
            {errors.agreeTerms && <div className="error">{errors.agreeTerms}</div>}
            <term-button onClick={() => setShowTermsModal(true)}>Ïù¥Ïö©ÏïΩÍ¥Ä Î≥¥Í∏∞</term-button>
            <button onClick={handleSignUp}>ÌöåÏõêÍ∞ÄÏûÖ</button>
            <br />
            <span onClick={handleCardSwitch}>Ïù¥ÎØ∏ Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî? Î°úÍ∑∏Ïù∏</span>
          </div>
        </div>
      </div>
      {showTermsModal && (
        <div className={`terms-modal-overlay ${isClosing ? 'fade-out' : ''}`}>
          <div className={`terms-modal-content ${isClosing ? 'pop-out' : ''}`}>
            <button className="terms-close-button" onClick={handleCloseModal}>
              &times;
            </button>
            <ReactMarkdown className="markdown-content">{termsContent}</ReactMarkdown>
          </div>
        </div>
      )}
      {toast.message && <Toast message={toast.message} type={toast.type} />}
    </div>
  );
};

export default SignIn;
