import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import TMDbAPI from '../services/URL.ts';
import MovieCard from '../components/MovieCard';
import MovieModal from '../components/MovieModal';
import '../styles/Search.css';

const Search = () => {
  const navigate = useNavigate();
  
  const [query, setQuery] = useState('');
  const [genres, setGenres] = useState([]);
  const [selectedGenre, setSelectedGenre] = useState('');
  const [selectedRating, setSelectedRating] = useState('');
  const [selectedLanguage, setSelectedLanguage] = useState('');
  const [selectedYear, setSelectedYear] = useState(''); // Í∞úÎ¥âÎÖÑÎèÑ
  const [selectedCertification, setSelectedCertification] = useState(''); // ÏòÅÌôî Îì±Í∏â
  const [movies, setMovies] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selectedMovie, setSelectedMovie] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);
  const [showButton, setShowButton] = useState(false);
  const [wishlist, setWishlist] = useState([]);
  const [apiKey, setApiKey] = useState(() => {
    const loggedInUser = JSON.parse(localStorage.getItem('loggedInUser'));
    return loggedInUser?.apiKey || null;
  });

  // Ï∞ú Î™©Î°ù Î°úÎìú Ìï®Ïàò
  const loadWishlist = () => {
    const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    setWishlist([...storedWishlist]); // ÏÉàÎ°úÏö¥ Î∞∞Ïó¥Î°ú ÏóÖÎç∞Ïù¥Ìä∏Ìï¥ ReactÍ∞Ä Í∞êÏßÄ
  };

  useEffect(() => {
    loadWishlist(); // Ï¥àÍ∏∞ Î°úÎìú Ïãú Ï∞ú Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
  }, []);
  
  useEffect(() => {
    if (!apiKey) {
      navigate('/signin');
    }
  }, [apiKey, navigate]);

  useEffect(() => {
    const fetchGenres = async () => {
      if (!apiKey) return;

      try {
        const genreData = await TMDbAPI.getGenres(apiKey);
        setGenres(genreData.genres);
      } catch (error) {
        console.error('Error fetching genres:', error);
      }
    };

    fetchGenres();
  }, [apiKey]);

  const fetchMovies = async (reset = false) => {
    if (!apiKey || loading) return;

    setLoading(true);

    try {
      let url;
      const queryParams = [
        `api_key=${apiKey}`,
        `language=ko-KR`,
        `page=${page}`,
      ];

      if (query) {
        url = `https://api.themoviedb.org/3/search/movie`;
        queryParams.push(`query=${encodeURIComponent(query)}`);
      } else {
        url = `https://api.themoviedb.org/3/discover/movie`;
        if (selectedGenre) queryParams.push(`with_genres=${selectedGenre}`);
        if (selectedRating) queryParams.push(`vote_average.gte=${selectedRating}`);
        if (selectedYear) queryParams.push(`primary_release_year=${selectedYear}`);
        if (selectedCertification) queryParams.push(`certification_country=US&certification=${selectedCertification}`);
        if (selectedLanguage) queryParams.push(`with_original_language=${selectedLanguage}`);
      }

      const response = await axios.get(`${url}?${queryParams.join('&')}`);
      let results = response.data.results;

      if (reset) {
        setMovies(results);
      } else {
        setMovies((prev) => [...prev, ...results]);
      }

      setHasMore(results.length > 0);
    } catch (error) {
      console.error('Error fetching movies:', error);
      setMovies([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    setMovies([]);
    setPage(1);
    setHasMore(true);
    fetchMovies(true);
  }, [query, selectedGenre, selectedRating, selectedLanguage, selectedYear, selectedCertification]);

  useEffect(() => {
    if (page > 1) {
      fetchMovies();
    }
  }, [page]);

  const handleScroll = () => {
    if (
      window.innerHeight + document.documentElement.scrollTop >=
      document.documentElement.offsetHeight - 200 &&
      hasMore &&
      !loading
    ) {
      setPage((prev) => prev + 1);
    }

    if (window.scrollY > 300) {
      setShowButton(true);
    } else {
      setShowButton(false);
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [hasMore, loading]);

  const handleReset = () => {
    setQuery('');
    setSelectedGenre('');
    setSelectedRating('');
    setSelectedLanguage('');
    setSelectedYear('');
    setSelectedCertification('');
    setMovies([]);
    setPage(1);
    setHasMore(true);
  };

  const handleMovieClick = (movie) => {
    const fetchWishlist = () => {
      const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
      setWishlist(storedWishlist);
    };

    fetchWishlist();
    setSelectedMovie(movie);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setSelectedMovie(null);
    setIsModalOpen(false);
    const updatedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    setWishlist(updatedWishlist);
  };

  const handleWishlistToggle = (movie) => {
    const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    const isWishlisted = storedWishlist.some((item) => item.id === movie.id);

    if (isWishlisted) {
      const updatedWishlist = storedWishlist.filter((item) => item.id !== movie.id);
      localStorage.setItem('wishlist', JSON.stringify(updatedWishlist));
    } else {
      storedWishlist.push(movie);
      localStorage.setItem('wishlist', JSON.stringify(storedWishlist));
    }
    loadWishlist();
  };

  useEffect(() => {
    if (!isModalOpen) {
      const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
      setWishlist(storedWishlist);
    }
  }, [isModalOpen]);
  

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  return (
    <div className="search">
      <h1>üßê Ïñ¥Îñ§ ÏòÅÌôîÎ•º Ï∞æÏúºÏãúÎÇòÏöî?</h1>
      <div className="filters">
        <input
          type="text"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="Ï†úÎ™© ÎòêÎäî ÌÇ§ÏõåÎìú ÏûÖÎ†•"
        />
        <br />
        <select value={selectedGenre} onChange={(e) => setSelectedGenre(e.target.value)}>
          <option value="">Ïû•Î•¥ (Ï†ÑÏ≤¥)</option>
          {genres.map((genre) => (
            <option key={genre.id} value={genre.id}>
              {genre.name}
            </option>
          ))}
        </select>
        <select value={selectedRating} onChange={(e) => setSelectedRating(e.target.value)}>
          <option value="">ÌèâÏ†ê (Ï†ÑÏ≤¥)</option>
          {[...Array(10)].map((_, i) => (
            <option key={i + 1} value={i + 1}>
              {i + 1} Ïù¥ÏÉÅ
            </option>
          ))}
        </select>
        <select value={selectedLanguage} onChange={(e) => setSelectedLanguage(e.target.value)}>
          <option value="">Ïñ∏Ïñ¥ (Ï†ÑÏ≤¥)</option>
          <option value="en">ÏòÅÏñ¥ ENG</option>
          <option value="ko">ÌïúÍµ≠Ïñ¥ KOR</option>
          <option value="ja">ÏùºÎ≥∏Ïñ¥ JPN</option>
          <option value="zh">ÎØ∏ÏñÄÎßàÏñ¥ MYA</option>
          <option value="de">ÎèÖÏùºÏñ¥ DEU</option>
          <option value="it">Ïù¥ÌÉúÎ¶¨Ïñ¥ ITA</option>
          <option value="ru">Îü¨ÏãúÏïÑÏñ¥ RUS</option>
          <option value="es">Ïä§ÌéòÏù∏Ïñ¥ ESP</option>
          <option value="fr">ÌîÑÎûëÏä§Ïñ¥ FRA</option>
        </select>
        <select value={selectedYear} onChange={(e) => setSelectedYear(e.target.value)}>
          <option value="">Í∞úÎ¥âÎÖÑÎèÑ (Ï†ÑÏ≤¥)</option>
          {Array.from({ length: 50 }, (_, i) => (
            <option key={2023 - i} value={2023 - i}>
              {2023 - i}
            </option>
          ))}
        </select>
        <select value={selectedCertification} onChange={(e) => setSelectedCertification(e.target.value)}>
          <option value="">ÏòÅÌôî Îì±Í∏â (Ï†ÑÏ≤¥)</option>
          <option value="G">G - Ï†ÑÏ≤¥ Í¥ÄÎûåÍ∞Ä</option>
          <option value="PG">PG - ÏùºÎ∂Ä Í¥ÄÎûåÍ∞Ä</option>
          <option value="PG-13">PG-13 - 13ÏÑ∏ Ïù¥ÏÉÅ</option>
          <option value="R">R - 17ÏÑ∏ Ïù¥ÏÉÅ</option>
          <option value="NC-17">NC-17 - 18ÏÑ∏ Ïù¥ÏÉÅ</option>
        </select>
        <button type="button" onClick={handleReset}>
          Ï¥àÍ∏∞Ìôî
        </button>
      </div>

      {loading && page === 1 ? (
        <p>Î°úÎî© Ï§ë...</p>
      ) : movies.length === 0 ? (
        <p>Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
      ) : (
        <div className="movie-grid">
          {movies.map((movie) => (
            <MovieCard
            key={`${movie.id}-${wishlist.some((item) => item.id === movie.id)}`} // ÏÉÅÌÉú Î≥ÄÌôî Í∞ïÏ†ú Î†åÎçîÎßÅ
            movie={movie}
            isWishlisted={wishlist.some((item) => item.id === movie.id)}
            onWishlistToggle={handleWishlistToggle}
            onClick={() => handleMovieClick(movie)}
            />
          ))}
        </div>
      )}

      {loading && page > 1 && <p>Ï∂îÍ∞Ä Î°úÎî© Ï§ë...</p>}

      {isModalOpen && selectedMovie && <MovieModal movie={selectedMovie} onClose={handleCloseModal} />}

      {showButton && (
        <button className="scroll-to-top" onClick={scrollToTop}>
          ‚Üë
        </button>
      )}
    </div>
  );
};

export default Search;
