import React, { useEffect, useState } from 'react';
import TMDbAPI from '../services/URL.ts';
import MovieCard from '../components/MovieCard';
import MovieModal from '../components/MovieModal'; // Î™®Îã¨ Ïª¥Ìè¨ÎÑåÌä∏ Ï∂îÍ∞Ä
import LoadingSpinner from '../components/LoadingSpinner';
import '../styles/Popular.css';

const Popular = () => {
  const apiKey = JSON.parse(localStorage.getItem('loggedInUser'))?.apiKey;

  const [popular, setPopular] = useState([]); // ÏòÅÌôî Îç∞Ïù¥ÌÑ∞
  const [loading, setLoading] = useState(false); // Î°úÎî© ÏÉÅÌÉú
  const [page, setPage] = useState(1); // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
  const [hasMore, setHasMore] = useState(true); // Îçî Î∂àÎü¨Ïò¨ Îç∞Ïù¥ÌÑ∞Í∞Ä ÏûàÎäîÏßÄ Ïó¨Î∂Ä
  const [selectedMovie, setSelectedMovie] = useState(null); // ÏÑ†ÌÉùÎêú ÏòÅÌôî
  const [isModalOpen, setIsModalOpen] = useState(false); // Î™®Îã¨ ÏÉÅÌÉú
  const [showButton, setShowButton] = useState(false); // Îß® ÏúÑÎ°ú Î≤ÑÌäº ÌëúÏãú ÏÉÅÌÉú
  const [wishlist, setWishlist] = useState([]);
  
  const loadWishlist = () => {
    const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    setWishlist([...storedWishlist]); // ÏÉàÎ°úÏö¥ Î∞∞Ïó¥Î°ú ÏóÖÎç∞Ïù¥Ìä∏Ìï¥ ReactÍ∞Ä Í∞êÏßÄ
  };


  // Ï¥àÍ∏∞ Ï∞ú Î™©Î°ù Î°úÎìú
  useEffect(() => {
    loadWishlist();
  }, []);
  
  // API Ìò∏Ï∂ú Ìï®Ïàò
  const fetchPopularMovies = async (currentPage) => {
    if (!apiKey || loading) return;

    setLoading(true);

    try {
      const data = await TMDbAPI.getPopularMovies(apiKey, currentPage);
      setPopular((prevMovies) => [...prevMovies, ...data.results]);
      setHasMore(data.page < data.total_pages);
    } catch (error) {
      console.error('Error fetching popular movies:', error);
    } finally {
      setLoading(false);
    }
  };

  // Ï¥àÍ∏∞ Îç∞Ïù¥ÌÑ∞ Î°úÎìú Î∞è ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Ìò∏Ï∂ú
  useEffect(() => {
    fetchPopularMovies(page);
  }, [page, apiKey]);

  // Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
  const handleScroll = () => {
    if (
      window.innerHeight + document.documentElement.scrollTop >=
        document.documentElement.offsetHeight - 100 && // ÌïòÎã® 100px Í∑ºÏ≤ò
      !loading &&
      hasMore
    ) {
      setPage((prevPage) => prevPage + 1); // Îã§Ïùå ÌéòÏù¥ÏßÄ ÏöîÏ≤≠
    }

    // Îß® ÏúÑÎ°ú Î≤ÑÌäº ÌëúÏãú ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
    if (window.scrollY > 300) {
      setShowButton(true);
    } else {
      setShowButton(false);
    }
  };

  // Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏ Îì±Î°ù Î∞è Ìï¥Ï†ú
  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [loading, hasMore]);

  // Îß® ÏúÑÎ°ú Ïä§ÌÅ¨Î°§
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  // ÏòÅÌôî ÌÅ¥Î¶≠ Ïãú Î™®Îã¨ Ïó¥Í∏∞
  const handleMovieClick = (movie) => {
    const fetchWishlist = () => {
      const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
      setWishlist(storedWishlist);
    };

    fetchWishlist();
    setSelectedMovie(movie);
    setIsModalOpen(true);
  };

  // Î™®Îã¨ Îã´Í∏∞
  const handleCloseModal = () => {
    setSelectedMovie(null);
    setIsModalOpen(false);
    const updatedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    setWishlist(updatedWishlist);
  };

  const handleWishlistToggle = (movie) => {
    const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
    const isWishlisted = storedWishlist.some((item) => item.id === movie.id);
  
    if (isWishlisted) {
      const updatedWishlist = storedWishlist.filter((item) => item.id !== movie.id);
      localStorage.setItem('wishlist', JSON.stringify(updatedWishlist));
    } else {
      storedWishlist.push(movie);
      localStorage.setItem('wishlist', JSON.stringify(storedWishlist));
    }
    setWishlist([...storedWishlist]); // Ï∞ú ÏÉÅÌÉú Î≥ÄÍ≤Ω ÌõÑ Í∞±Ïã†
  };
  

  

  useEffect(() => {
    if (!isModalOpen) {
      const storedWishlist = JSON.parse(localStorage.getItem('wishlist')) || [];
      setWishlist(storedWishlist);
    }
  }, [isModalOpen]);  

  return (
    <div className="popular">
      <h1>üèÜ ÏßÄÍ∏à Îú®Îäî Ïª®ÌÖêÏ∏†</h1>
      <div className="movie-grid">
        {popular.map((movie) => (
          <MovieCard
          key={`${movie.id}-${wishlist.some((item) => item.id === movie.id)}`} // ÏÉÅÌÉú Î≥ÄÌôî Í∞ïÏ†ú Î†åÎçîÎßÅ
          movie={movie}
          isWishlisted={wishlist.some((item) => item.id === movie.id)}
          onWishlistToggle={handleWishlistToggle}
          onClick={() => handleMovieClick(movie)}
          />
        ))}
      </div>
      {loading && <LoadingSpinner />}
      {!hasMore && <p className="end-message">Î∂àÎü¨Ïò¨ ÏòÅÌôîÍ∞Ä ÎçîÏù¥ÏÉÅ ÏóÜÏäµÎãàÎã§...!</p>}

      {/* Îß® ÏúÑÎ°ú Î≤ÑÌäº */}
      {showButton && (
        <button className="scroll-to-top" onClick={scrollToTop}>
          ‚Üë
        </button>
      )}

      {/* Î™®Îã¨ */}
      {isModalOpen && selectedMovie && (
        <MovieModal movie={selectedMovie} onClose={handleCloseModal} />
      )}
    </div>
  );
};

export default Popular;
